import axios from 'axios';



export default class ProductHelper {
    static instance = new ProductHelper();

    constructor() {
        this.loadCart();
    }
    cart = []
    products = [];
    currentPage = -1;
    totalItem = 0;

    setCart(cart) {
        this.cart = cart
        this.saveCart()
    }

    saveCart() {
        console.log("json = ", JSON.stringify(this.cart))
        localStorage.setItem("cart", JSON.stringify(this.cart))
    }

    loadCart() {
        console.log('load = ', localStorage.getItem("cart"))
        this.cart = JSON.parse(localStorage.getItem("cart")) ?? []
    }

    getAllProducts() {
        return axios.get('/products');
    }

    getProductsByPage(page, text) {
        if (page == 0) {
            this.products = [];
            this.currentPage = 0;
            this.totalItem = 0;
            // currentPage = 0;
        }
        this.currentPage = page;
        return axios.get('/products', {
            params: {
                "page": page,
                "q": text,
            },
            baseURL: "https://us-central1-shopeeassistant-28935.cloudfunctions.net/expressApi3/"
        });
    }

    getSellingProducts() {
        return axios.get('/allSellingProducts', {
            baseURL: "https://us-central1-shopeeassistant-28935.cloudfunctions.net/expressApi2/"
        });
    }

    addProduct(product, quantity) {
        console.log("add product", product)
        if (quantity == null) {
            quantity = 1;
        }
        var found = false;
        for (var i = 0; i < this.cart.length; i++) {
            var cartItem = this.cart[i];
            if (cartItem.product['code'] === product['code']) {
                cartItem.quantity = cartItem.quantity + quantity;
                found = true;
                this.cart[i] = cartItem;
                break;
            }
        }
        if (found === false) {
            this.cart.push({ product: product, quantity: quantity });
           
        }
        this.saveCart()
    }

   
    removeProducts(product) {
        for (var i = (this.cart.length - 1); i >= 0; i--) {
            var cartItem = this.cart[i];
            if (cartItem.product['code'] === product['code']) {
                this.cart.splice(i, 1);
                this.saveCart()
                break;
            }
        }
    }

    getCartCount() {
        if (this.cart == null) {
            return 0;
        }
        var count = 0;
        for (var i = 0; i < this.cart.length; i++) {
            var item = this.cart[i];
            count = count + item.quantity;
        }
        return count;
    }



    getCartAmount() {
        if (this.cart == null) {
            return 0;
        }
        var total = 0;
        for (var i = 0; i < this.cart.length; i++) {
            var item = this.cart[i];
            total = total + (item.quantity * parseInt(item.product["basePrice"]));
        }
        return total;
    }

    sendOrder(name, phone, address, note) {
        var cartJson = [];
        for (var i = 0; i < this.cart.length; i++) {
            var item = this.cart[i];
            cartJson.push({
                fullName: item.product['fullName'],
                basePrice: item.product['basePrice'],
                code: item.product['code'],
                quantity: item.quantity,
            });
        }

        var data = {
            cart: cartJson,
            name: name,
            phone: phone,
            address: address,
            note: note
        };
        console.log('json cart', data)
        return axios.post('/order',
            data,
            {
                baseURL: 'https://us-central1-shopeeassistant-28935.cloudfunctions.net/expressApi2/',
            });
    }
}