import React, { useState } from "react";
import { Container, Nav, Navbar, Image, InputGroup, FormControl, Modal } from "react-bootstrap";
import './index.css'
import logoImg from "./images/pink-logo.png";
import Button from "@restart/ui/esm/Button";
import { CartPage } from "./cart";

import cartIcon from "./images/img-shopping-cart.svg";
export class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            searchedText: "",
        };
    }
    render() {
        return (
            <Navbar className='pinkNav' fixed="top">{/* collapseOnSelect expand="lg" */}
                <Container fluid>
                    <Nav style={{ width: '80%' }}>
                        <Navbar.Brand href="/#/" onClick={(event) => {
                            this.setState({ searchedText: "" }, () => {
                                this.props.onClickSearch(this.state.searchedText)
                            });

                        }}>
                            <Image src={logoImg} style={{ width: '50px', height: '50px' }}></Image>
                        </Navbar.Brand>

                        <InputGroup className="mb-3" style={{ marginTop: '12px' }}>
                            <FormControl
                                placeholder="Nhập để tìm kiếm (tên, mã sản phẩm)"
                                aria-label=""
                                value={this.state.searchedText}
                                aria-describedby="basic-addon2"
                                onChange={(event) => {
                                    this.setState({ searchedText: event.target.value })
                                }}
                                onKeyUp={(event) => {
                                    this.props.onClickSearch(this.state.searchedText)
                                }}
                            />
                            <Button onClick={() => {
                                console.log('on click search button with ', this.state.searchedText)
                                this.props.onClickSearch(this.state.searchedText)
                            }}>
                                Search
                            </Button>
                        </InputGroup>
                    </Nav>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="me-auto">
                        </Nav>
                        <Nav style={{marginRight: '8%'}}>
                            <CartModel  cartItemCount={this.props.cartItemCount} onChanged={this.props.onChanged}/>
                        </Nav>
                    </Navbar.Collapse>
                </Container >
            </Navbar >
        );
    }
}

function CartModel(props) {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
        
            <Nav.Link className="includer" onClick={handleShow}>
            <Image  src={cartIcon} width={24} height={24} style={{}}></Image>   
                <span class="badge badge-success">{props.cartItemCount}</span>
                </Nav.Link>
            <Modal size="lg" show={show} onHide={handleClose}>
                <CartPage onChanged={props.onChanged}/>
            </Modal>
        </>
    );
}